.container-about {
    max-width: 700px;   
    background: #fff;
    margin: 0px auto  0px; 
    box-shadow: 1px 1px 2px #DAD7D7;
    /* border-radius: 3px;   */
    padding: 40px;
    margin-bottom: 60px;
    /* margin-top: 50px;
    margin-bottom: 50px; */
    /* border: 1px solid black; */
    box-shadow:0px 25px 40px rgba(0,0,0,.5);
}
  
.header {
    margin-bottom: 30px;
}

.full-name {
      font-size: 40px;
      text-transform: uppercase;
      margin-bottom: 5px;
}
    
.first-name {
    font-weight: 700;
    color: red;
}
    
.last-name {
    font-weight: 300;
}
    
.contact-info {
    margin-bottom: 20px;
}
    
.email ,
.phone {
    color: #999;
    font-weight: 300;
} 

.separator {
    height: 10px;
    display: inline-block;
    border-left: 2px solid #999;
    margin: 0px 10px;
 }
      
.position {
    font-weight: bold;
    display: inline-block;
    margin-right: 10px;
    text-decoration: underline;
    margin-bottom: 20px;
    font-size: 20px;
}
    
    
.details {
    line-height: 20px;
}
      
.section {
    margin-bottom: 40px;  
    
}
.section_list{
    margin-bottom: 20px;
}
      
.section:last-of-type {
    margin-bottom: 0px;  
}
      
.section__title {
    letter-spacing: 2px;
    color: #54AFE4;
    font-weight: bold;
    margin-top: 40px;
    margin-bottom: 10px;
    text-transform: uppercase;
    text-decoration: underline;
    font-size: 20px;
}
      
.section__list-item {
    margin-bottom: 40px;
    font-weight: bold;
}
      
.section__list-item:last-of-type {
    margin-bottom: 20px;
}

.left ,
.right {
    vertical-align: top;
    display: inline-block;
    margin-bottom: 20px;
}

.left {
    width: 60%;    
}

.right {
    text-align: right;
    width: 39%;    
}

.name {
    font-weight: bold;
}

/* a {
text-decoration: none;
color: #000;
font-style: italic;
} */

a:hover {
    text-decoration: underline;
    color: #000;
}
              
.skills__item {
    margin-bottom: 10px;  
}

.skills__item .right {
/* input { */
    display: none;
}

label {
    display: inline-block;  
    width: 20px;
    height: 20px;
    background: #C3DEF3;
    border-radius: 20px;
    margin-right: 3px;
}

input:checked + label {
    background: #79A9CE;
}


